import React, { useState } from 'react'
// import { Link } from 'react-router-dom'
import CommonAccountBg from 'components/CommonAccountBg/CommonAccountBg';
import './account.less'
import { loginApi } from 'Api/login';
import Http from 'utils/Http';
// import Verify from './Verify';
import CreateAccount from './CreateAccount';
import ResetPwd from './ResetPwd';
import VerifyAccount from './VerifyAccount';
import Login from './Login';
import WeChatLogin from './WeChatLogin';
import PhoneCreate from './PhoneCreate';
interface AccountManage {
    match: any,
}
function Account(props: AccountManage) {
    const { match } = props
    const [loginStep, setLoginStep] = useState<string>(match.params.step); // 步骤 默认为验证账号
    const [phone, setPhone] = useState<string>(''); // 验证手机
    const [historyStep, setHistoryStep] = useState<string>('1'); // 验证手机
    // 返回上一步
    function returnPage() {
        setLoginStep(historyStep);
    }
    // 验证手机号
    function verifyAccount(value: any) {
        setHistoryStep('1');
        setPhone(value.phone);
        Http.get(`${loginApi}?phone=${phone}`).then(res => {
            if (res.data) {
                setLoginStep('2');
            }
        })
    }
    // 创建账号
    function createAccount(value: any) {
        setHistoryStep('2');
        setPhone(value.phone);
        Http.get(`${loginApi}?phone=${phone}`).then(res => {
            if (res.data) {
                setLoginStep('3');
            }
        })
    }
    function resetPwd() {
        setHistoryStep('3');
        console.log('重置密码')
    }
    // 登录
    function login() {
        setHistoryStep('4');
        console.log('登录')
    }
    // 忘记密码
    function forgetPwd() {
        console.log('忘记密码');
    }


    return (
        <div >
            <CommonAccountBg>
                {loginStep === '1' ? <VerifyAccount verifyAccount={verifyAccount} /> : ''}
                {loginStep === '2' ? <CreateAccount createAccount={createAccount} returnPage={returnPage} phoneNum={phone} /> : ''}
                {loginStep === '3' ? <ResetPwd resetPwd={resetPwd} returnPage={returnPage} phoneNum={phone} /> : ''}
                {loginStep === '4' ? <Login login={login} returnPage={returnPage} forgetPwd={forgetPwd} phoneNum={phone} /> : ''}
                {loginStep === '5' ? <WeChatLogin returnPage={returnPage} /> : ''}
                {loginStep === '6' ? <PhoneCreate createAccount={createAccount} returnPage={returnPage} phoneNum={phone}  /> : ''}
            </CommonAccountBg>

        </div>
    )
}

export default Account
