import React from 'react'
import { Form, Input, Select } from 'antd';
import SVG from 'components/SVG';
import './login.less'
const loginBtnBg = require('assets/account/btn-bg@2x.png')
const { Option } = Select;
interface LoginProps {
    phoneNum: string,
    login: (params: any) => void,
    returnPage: () => void,
    forgetPwd: () => void
}

function Login(props: LoginProps) {
    const { login, returnPage,forgetPwd } = props;
    const prefixSelector = (
        <Select defaultValue='86' style={{
            width: 70
        }}>
            <Option value="86">+86</Option>
            <Option value="87">+87</Option>
        </Select>
    );
    return (
        <div className='login-box animate-route'>
            <p className='login-box-title'>解锁新技能,上天赋树</p>
            <div className='login-form'>
                <div onClick={returnPage}>
                    <SVG xlinkHref='icon-fanhui' className='reset-return' />
                </div>
                <p className='login-form-title'>
                    登录账户
                            </p>
                <Form name='login' onFinish={login}>
                    <Form.Item
                        name="phone">
                        <Input className='login-form-account' addonBefore={prefixSelector} style={{ width: '100%' }} />
                    </Form.Item>
                    <Form.Item
                        name="pwd" className='login-form-input' >
                        <Input.Password placeholder='请输入密码' />
                    </Form.Item>
                    <Form.Item>
                        <button className='login-btn' type='submit'>
                            <img src={loginBtnBg} alt="登录按钮背景" />
                                登录</button>
                    </Form.Item>
                    <Form.Item>
                        <div className='login-mode' onClick={forgetPwd}>
                            <p>忘记密码？</p>
                        </div>
                    </Form.Item>
                </Form>
            </div>
        </div>
    )
}

export default Login
