import axios from 'axios'
import { message } from 'antd';
import { TOKEN } from './localStorageConfig';
import Cookie from './cookie';
import { clearSessionStorage } from './sessionStorage'
let win: any = window
// create an axios instance
const Http = axios.create({
  baseURL: process.env.NODE_ENV === 'development' ? 'http://localhost:3000' : '', // api的base_url
  timeout: 20000 // request timeout
})

// request interceptor
Http.interceptors.request.use(config => {
  let token = Cookie.get(TOKEN)
  if (token) {
    config.headers['token'] = token// 让每个请求携带token-- ['X-Token']为自定义key 请根据实际情况自行修改
  }
  return config
}, error => {
  Promise.reject(error)
})

Http.interceptors.response.use(
  response => {
    const res = response.data;
    if (res.code !== 200) {
      if (res.code === '10007') {
        message.error('登陆信息已过期,请重新登陆!');
        setTimeout(() => {
          win.location.href = '/'
          localStorage.clear();
          clearSessionStorage();
          Cookie.clear()
        }, 1000)
      } else if (res.code === '10008') {
        message.error(`${res.exception}`);
      } else {
        message.error(`${res.statusText || res.exception}`);
      }
      return Promise.reject(res.data);
    } else {
      return res;
    }
  },
  error => {
    if (error.response && error.response.status === 401) {
      if (error.config.url.indexOf("logout") === -1) {
        message.error('登陆信息已过期,请重新登陆!');
      }
      setTimeout(() => {
        win.location.href = '/'
        localStorage.clear();
        clearSessionStorage();
        Cookie.clear()
      }, 1000)

    } else if (error.response && error.response.status === 500) {
      message.error('系统错误!');
    } else if (error.message && error.message.indexOf("timeout") > -1) {
      message.error('网络超时!');
    }
    else if (error === "403") {
      message.error('没有请求权限!');
    } else {
      message.error('网络错误!');
    }
    return Promise.reject(error)
  })

export default Http