import React, { ReactElement } from 'react'
interface ISVG {
    xlinkHref?: string
    className?: string
}
const SVG: (prop: ISVG) => ReactElement = (prop: ISVG) => {
    const { xlinkHref, className } = prop
    return (
        <svg className={`icon ${className}`} aria-hidden="true">
            <use xlinkHref={`#${xlinkHref}`}></use>
        </svg>
    )
}

export default SVG
